.Container {
  position: fixed;
  height: 400px;
  width: 600px;
  top: 20px;
  left: 50%;
  margin-left: -300px;
  z-index: 0;
  background: grey;
}

.Link {
  padding: 10px;
  display: block;
  line-height: 30px;
  text-decoration: none;
  color:black;
  font-weight:400
}

.Link:hover{
    transition-duration:3s;
    background:lightgrey
}

.Selection {
  font-weight: 900;
  padding: 20px;
  text-align: center;
}
